<template>
	<div class="create_ec">
		<div class="create_top">
			<p><span @click="goBack">返回</span></p>
			<p><span>电商小程序</span><br /><span>在线售卖实体商品</span></p>
			<p><span>￥ <span style="font-size: 0.18rem;">{{value.price}}</span>/{{value.year_num}}年</span></p>
		</div>
		<div class="create_list">
		 <transition name="fade">
			<div class="create_img" v-for="(ec,index) in ecs" @mouseenter="listData(index,ec)" @mouseleave="listUp(index,ec)">
				<img :src="ec.parite" v-show="ec.last" />
				<div class="create_but" v-show="!ec.last">
					<p class="ceate_title"><span>美妆电商</span></p>
					<p class="ceate_zhifu"><span>浏览商品</span></p>
					<p class="ceate_zhifu"><span>在线支付</span></p>
					<p class="ceate_zhifu"><span>轮播图</span></p>
					<p class="ceate_zhifu"><span>商品搜索</span></p>
					<p class="ceate_button">
						<button type="button" @click="pictUp(index,ec)">选择该模板</button>
					</p>
					<p class="ceate_erwei">
						<img src="../../assets/liantu.png" />
					</p>
					<p class="ceate_zhifu"><span>使用微信扫码登录</span></p>
				</div>
			</div>
		</transition>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				fee: '',
				ectrue: false,
				last: true,
				ecs: [{
						last: true,
						parite: require('../../assets/zhou.jpg'),

					}]

			}
		},
    props: ["value"],
    watch:{
      value (val) {
        return val;
      }
    },
		methods: {
			goBack() {
				this.$store.state.ec = false;
				this.$store.state.ecContent = true;
			},
			listData(index,ec) {
				this.ecs[index].last = false;
				this.ecs.splice(index, 1, this.ecs[index])
			},
			listUp(index,ec) {
				this.ecs[index].last = true;
				this.ecs.splice(index, 1, this.ecs[index])
			},
			pictUp(index){
				this.post("custom/Own/createUserApp", {
						type: 1,
            setting_id: this.value.id
					}).then(res => {
						if(res.data.code === 10000) {
							const content ="恭喜你创建成功";
							this.$Modal.success({
								title: "创建成功",
								content: content
							});
              sessionStorage.setItem('appid',res.data.data.appid);
							clearTimeout(timer);
							let timer=setTimeout(()=>{
                let url=`/${res.data.data.appid}`+'/editor';
                this.$router.push(url);
              },2000);
						} else {
							const content = res.data.msg_test;
							this.$Modal.error({
								title: "创建失败",
								content: content
							});
						}
					})
			}
		},
		//	mounted(){
		//    this.get('custom/Base/app_type').then(res=>{
		//    	 console.log(res);
		//    })
		//  },
	}
</script>

<style scoped="scoped">
	.create_ec {
		margin: 0.4rem auto;
		width: 60%;
	}
	.create_top {
		display: flex;
		justify-content: center;
	}

	.create_top>p {
		flex: 1;
		text-align: center;
	}

	.create_top>p:nth-child(1) {
		flex: 1;
		text-align: left;
	}

	.create_top>p:nth-child(3) {
		flex: 1;
		text-align: right;
	}

	.create_top>p:nth-child(1)>span {
		display: inline-block;
		width: 0.6rem;
		height: 0.3rem;
		line-height: 0.3rem;
		background: #969896;
		color: white;
		text-align: center;
		border-radius: 0.05rem;
	}

	.create_top>p:nth-child(1)>span:hover {
		background: #9E9394;
		cursor: pointer;
	}

	.create_top>p:nth-child(2)>span:nth-child(1) {
		font-size: 0.22rem;
		color: #636972;
	}

	.create_top>p:nth-child(2)>span:nth-child(2) {
		color: #636972;
	}

	.create_list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	.create_img {
		border: 0.02rem solid #DADADA;
		position: relative;
		width: 3rem;
		height: 5.4rem;
		margin: 0.3rem;
	}

	.create_img>img {
		width: 100%;
		height: 100%;
	}

	.create_fan {
		border: 0.02rem solid #DADADA;
		position: relative;
    width: 3rem;
		margin: 0.2rem;
	}

	.ceate_title {
		display: flex;
		justify-content: center;
		margin-top: 0.2rem;
		margin-bottom: 0.1rem;
		font-size: 0.24rem;
	}

	.ceate_zhifu {
		display: flex;
		justify-content: center;
	}

	.ceate_zhifu>span {
		color: #c8cbcd;
	}

	.ceate_title>span {
		font-size: 0.2rem;
		color: #80868C;
	}

	.ceate_button {
		display: flex;
		justify-content: center;
		margin-top: 0.2rem;
	}

	.ceate_button>button {
		width: 2rem;
		height: 0.4rem;
		border: none;
		background: #A5CD1D;
		color: #FAFCF3;
		font-size: 0.17rem;
		border-radius: 0.03rem;
	}

	.ceate_erwei {
		margin: 0.2rem auto;
		width: 2rem;
		height: 1.6rem;
		border-top: 1px solid #E6E6E6;
	}

	.ceate_erwei>img {
		width: 100%;
		height: 100%;
		height: 1.6rem;
	}
	.fade-enter-active,
	.fade-leave-active {
		transition: opacity .5s
	}

	.fade-enter,
	.fade-leave-to {
		transition: opacity .5s
	}
</style>



