<template>
	<div>
		<div class="create">
		  <topBar></topBar>
		</div>
		<div class="content" v-show="$store.state.ecContent">
			<h2>
        	请选择一个小程序类型
      </h2>
			<p>
				不同的类型具有不同的功能与使用场景。
			</p>
			<div class="type">
				<Tooltip content="在线售卖实体商品" placement="top">
					<a href="javascript:void(0)" @click="create(1)">电商</a>
				</Tooltip>
				<Tooltip content="在线预约理发、培训、看房等服务" placement="top">
					<a href="javascript:void(0)" @click="create(2)">服务预订</a>
				</Tooltip>
        <Tooltip content="在线预定酒店房间" placement="top">
          <a href="javascript:void(0)" @click="create(3)">酒店预订</a>
        </Tooltip>
			</div>
			<div class="create_img">
				<img src="../../assets/tupaian.png" />
			</div>
		</div>
		<div class="create_ec">
			<ec v-if="$store.state.ec" :value="meal"></ec>
			<ection v-if="$store.state.ection" :value="meal"></ection>
      <hotel v-if="show" :value="meal"></hotel>
		</div>
	</div>
</template>
<script>
	import ec from './ec.vue'
	import ection from './ection.vue'
	import topBar from './topBar.vue'
	import hotel from './hotelBook.vue'
	export default {
		data() {
			return {
        show:false,
				imgurl: "",
				imgurl2: "",
        meal:''
			}
		},
		methods: {
			create(val) {
				if(val === 1) {
					this.$store.state.ec = true;
					this.$store.state.ecContent = false;
				} else if(val === 2){
					this.$store.state.ection = true;
					this.$store.state.ecContent = false;
				}else if(val === 3){
            this.$store.state.ecContent = false;
            this.show = true;
        }
				this.getAppMeal(val);
      },
    /*
    *  获取小程序套餐
    * */
    getAppMeal (val) {
       this.post("custom/Price/get_app_setting",{
         type: val
       }).then(res => {
          if (res.data.code === 10000) {
                this.meal = res.data.data;
          }
       })
    }
		},
		mounted() {
      if(this.$route.name==="createApp"){
        this.$store.dispatch("fetch_load" ,false)
      }
		},
		components: {
			ec,
			ection,
      hotel,
			topBar,
		}
	}
</script>
<style scoped>
	/*.create {
		width: 100%;
		height: 0.5rem;
		background: #8159BB;
	}*/

	.content {
		text-align: center;
		padding: 0.3rem;
	}

	h2 {
		font-size: 0.3rem;
		margin-bottom: 0.15rem;
	}

	p {
		font-size: 0.15rem;
	}

	.type {
		padding: 0.2rem;
	}

	.type a {
		display: flex;
		padding: 0.15rem 0.3rem;
		border: 1px solid #8159BB;
		border-radius: 0.1rem;
		font-size: 0.16rem;
		font-weight: 600;
		color: #8159BB;
		margin-right: 0.2rem;
	}

	.create_img>img {
		width: 80%;
	}
</style>



